import React, {useEffect, useState } from "react";
import { getDatabase, ref, onvalue, onDisconnect, set } from "firebase/database";
import { auth, apiKey } from "./firebase/firebase"; 
import useSetOnlineStatus from './hooks/useSetOnlineStatus';
import useOnlineCount from './hooks/useOnlineCount';

function App() {
  useSetOnlineStatus();
  const onlineCount = useOnlineCount();
  let sessionId = localStorage.getItem('sessionId');
  if (!sessionId) {
    sessionId = Math.random().toString(36).slice(2, 12);
    localStorage.setItem('sessionId', sessionId);
  }

  //     
  useSetOnlineStatus(sessionId);
  return (
   <div className="Main-Container">
    <div>
      <h2>  : {onlineCount}</h2>
    </div>
    <div className="Page-Title">
      Chattery
    </div>
    <div className="ProcessEnter-Container">
      <input className="NickNameInput" placeholder="   " autocomplete="off"/>
      <button className="ChatStart" onclick="chatstart()"></button>
    </div>
   </div>
  );
}

export default App;
